const  $siteList =$('.siteList')
const  $lastLi =$('.lastLi')
const hashLI = localStorage.getItem('x');
const  lObject = JSON.parse(hashLI);
let hashMap =  lObject ||[
{'logo':'A','url':"https://www.acfun.com"},
    {'logo':'B','url':"https://www.baidu.com"},
    {'logo':'T','url':"https://www.taobao.com"},
];

const simplyUrl= (url) => {
   return  url.replace('https://','')
        .replace('http://','')
        .replace('www.','')
        .replace(/\/.*/,'')//åˆ é™¤/å¼€å¤´æ‰€æœ‰çš„å†…å®¹
}
const refresh = function (){
    $siteList.find('li:not(.lastLi)').remove();
    hashMap.forEach((node,index) =>{
        const $li = $(`<li>
            <div class="site">
                <div class="logo">${node.logo}</div>
                <div class="link">${simplyUrl(node.url)}</div>
                <div class="deletebtn">
                    <svg class="icon" >
                        <use xlink:href="#icon-close"></use>
                     </svg>
                
                </div>
            </div>
    </li>`).insertBefore($lastLi)
        $li.on('click','.site',(e)=>{
            console.log('siteæ‰§è¡Œäº†')
           window.open(node.url);
        })

        $li.on('click','.deletebtn',(e)=>{
            console.log('closeæ‰§è¡Œäº†')
            e.stopPropagation();
            hashMap.splice(index,1)
            $li.remove();
            // refresh();
        })

    })

}

refresh();

$('.addButton')
.on('click',()=>{
    let url = window.prompt('è¯·é—®ä½ è¦æ·»åŠ çš„ç½‘å€æ˜¯ï¼Ÿ')
    if(url && url.indexOf('http')!==0){
        url = 'https://'+url;
    }
    hashMap.push({
       'logo': simplyUrl(url)[0].toUpperCase(),
        'url':url
        })
    refresh()

});

window.onbeforeunload = ()=>{
    const string = JSON.stringify(hashMap);//å¾—åˆ°æ˜¯å­—ç¬¦ä¸²
    localStorage.setItem('x',string)//localStorageæ˜¯å…¨å±€å˜é‡ï¼Œå­˜å‚¨ä»¥é”®å€¼å¯¹çš„å½¢å¼ã€‚å­˜åœ¨æœ¬åœ°å­˜å‚¨ã€‚
}

$(document).on('keypress',(e)=>{
console.log(e.key)
    const  {key} = e
    for (let i =0 ;i < hashMap.length;i++){
        if (key === hashMap[i].logo.toLowerCase()){
            window.open(hashMap[i].url)
        }
    }
})